
.page-link {
    color: #666;
    background-color: rgb(255, 255, 255);
    border-color: rgb(0,0,0,.048);
}
.page-link:hover {
    color: rgb(217, 83, 79);
    background-color: #FFF;
}
.page-item.active .page-link {
    z-index: 2;
    color: rgb(255, 255, 255);
    background-color: rgb(217, 83, 79);
    border-color: rgb(217, 83, 79);
}



